import { createAction, props } from '@ngrx/store';
import { ObraSocial, EspecialidadProfesional, Turno, Paciente } from '../../../shared/models/datos.models';
import { FiltrosPacientes, ConfirmacionTurno, LiberacionTurno } from '../../../shared/models/request.models';
import { Respuesta, TurnoPacienteRespuesta } from '../../../shared/models/responses.models';

// clear 
export const CLEAR = '[Turno] - clear';
export const clear = createAction(
    CLEAR
);

// os
export const SET_OBRA_SOCIAL_SELECTED = '[Turno] - setObraSocialSelected ';
export const setObraSocialSelected = createAction(
    SET_OBRA_SOCIAL_SELECTED,
    props<{obraSocialSelected: ObraSocial}>()
);

// profesional
export const SET_ESPECIALIDAD_PROFESIONAL_SELECTED = '[Turno] - setEspecialidadProfesionalSelected ';
export const setEspecialidadProfesionalSelected = createAction(
    SET_ESPECIALIDAD_PROFESIONAL_SELECTED,
    props<{especialidadProfesionalSelected: EspecialidadProfesional}>()
);


// turno
export const GET_TURNO = '[Turno] - getTurnoByCodigo';
export const getTurnoByCodigo = createAction(
    GET_TURNO,
    props<{ codigoTurno: number }>()
);

export const RTA_TURNO = '[Turno] - setTurnoRecuperado';
export const setTurnoRecuperado = createAction(
    RTA_TURNO,
    props<{ turnoPacienteRespuesta: TurnoPacienteRespuesta }>()
);

export const SET_TURNO_SELECTED = '[Turno] - setTurnoSelected ';
export const setTurnoSelected = createAction(
    SET_TURNO_SELECTED,
    props<{turnoSelected: Turno}>()
);

export const ASIGNAR_TURNO = '[Turno] - asignarTurno';
export const asignarTurno = createAction(
    ASIGNAR_TURNO,
    props<{codigoTurno: number}>()
);

export const RTA_ASIGNACION = '[Turno] - setAsignacion';
export const setAsignacion = createAction(
    RTA_ASIGNACION,
    props<{ rtaAsignacion: Respuesta }>()
);

export const CONFIRMA_TURNO = '[Turno] - confirmarTurno';
export const confirmarTurno = createAction(
    CONFIRMA_TURNO,
    props<{confirmaTurno: ConfirmacionTurno}>()
);

export const RTA_CONFIRMACION = '[Turno] - setConfirmacion';
export const setConfirmacion = createAction(
    RTA_CONFIRMACION,
    props<{ rtaConfirmacion: Respuesta }>()
);

export const LIBERA_TURNO = '[Turno] - liberarTurno';
export const liberarTurno = createAction(
    LIBERA_TURNO,
    props<{liberaTurno: LiberacionTurno}>()
);

export const RTA_LIBERACION = '[Turno] - setRtaLiberacion';
export const setLiberacion = createAction(
    RTA_LIBERACION,
    props<{ liberacion: Respuesta }>()
);


// lista pacientes

export const GET_PACIENTES = '[Turno] - getPacientes';
export const getPacientes = createAction(
    GET_PACIENTES,
    props<{ filtrosPacientes: FiltrosPacientes }>()
);

export const SET_PACIENTES= '[API] - setPacientes ';
export const setPacientes = createAction(
    SET_PACIENTES,
    props<{ pacientes: Paciente[] }>()
);

// paciente por codigo
export const GET_PACIENTE_CODIGO = '[Turno] - getPacienteCodigo ';
export const getPacienteByCodigo = createAction(
    GET_PACIENTE_CODIGO,
    props<{filtrosPacientes: FiltrosPacientes}>()
);

// paciente

export const GET_PACIENTE_SELECTED = '[Turno] - getPacienteSelected';
export const getPacienteSelected = createAction(
    GET_PACIENTE_SELECTED
);

export const SET_PACIENTE_SELECTED = '[Turno] - setPacienteSelected';
export const setPacienteSelected = createAction(
    SET_PACIENTE_SELECTED,
    props<{pacienteSelected: Paciente}>()
);

export const POST_PACIENTE = '[Turno] - postPaciente';
export const postPaciente = createAction(
    POST_PACIENTE,
    props<{ paciente: Paciente }>()
);
