import { createFeatureSelector, createSelector } from '@ngrx/store';
import { ObraSocial } from '../../../shared/models/datos.models';
import { Formulario } from '../../../shared/models/forms.models';
import { FiltrosLicencias, FiltrosProfesionales, FiltrosTurnos } from '../../../shared/models/request.models';

export const selectFormulario = createFeatureSelector<Formulario>('formulario');

export const selectAllLocalidades = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.localidades
);

// obras sociales
export const selectAllObrasSociales = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.obrasSociales
);

export const selectObraSocialSelected = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.obraSocialSelected
);

// planes
export const selectPlanes = createSelector(
    selectObraSocialSelected,
    (obraSocial: ObraSocial) => {
        if (obraSocial) {
            return obraSocial.plan;
        }
    }
);

// especialidades
export const selectAllEspecialidades = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.especialidades
);

export const selectEspecialidadSelected = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.especialidadSelected
);

// profesionales

export const selectAllProfesionales = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.especialidadesProfesionales
);

export const selectProfesionalesBusquedaA = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.especialidadesProfesionalesFiltradas
);

export const selectProfesionalSelected = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.especialidadProfesionalSelected
);

export const selectBusquedaProfesionales = createSelector(
    selectFormulario,
    (formulario: Formulario) => {
        const request = new FiltrosProfesionales();
        if (formulario.especialidadSelected != undefined) {
            request.esp = formulario.especialidadSelected.codigo.toString();
        }
        if (formulario.obraSocialSelected != undefined) {
            request.os = formulario.obraSocialSelected.codigo?.toString();
        }
        return request;
    }
);

// licencias

export const selecBusquedatLicencias = createSelector(
    selectFormulario,
    (formulario: Formulario) => {
        const request = new FiltrosLicencias();
        if (formulario.especialidadProfesionalSelected != undefined) {
            request.ep = formulario.especialidadProfesionalSelected.codigo.toString();
        }
        return request;
    }
);

export const selectLicencias = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.licencias
);

// turnos

export const selectBusquedaTurnos = createSelector(
    selectFormulario,
    (formulario: Formulario) => {
        const request = new FiltrosTurnos();
        if (formulario.fechaSelected != undefined) {
            request.fecha = 
                Number(formulario.fechaSelected.getFullYear()) + "-" +
                Number((formulario.fechaSelected.getMonth() + 1)) + "-" +
                Number(formulario.fechaSelected.getDate());
        }
        if (formulario.especialidadProfesionalSelected != undefined) {
            request.ep = formulario.especialidadProfesionalSelected.codigo.toString();
        }
        if (formulario.obraSocialSelected != undefined) {
            request.os = formulario.obraSocialSelected.codigo?.toString();
        }
        return request;
    }
);

export const selectTurnos = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.turnosRta
);

export const selectFechaTurnos = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.fechaSelected
);
