import { of } from 'rxjs';
import { delay } from 'rxjs/internal/operators';

const DEFAULT_DELAY = 1000;


export function getWsFromMock(mockup: any, delayMs?: number) {
    delayMs = delayMs ? delayMs : DEFAULT_DELAY;
    return of(mockup).pipe(delay(delayMs));
}

export function throwErrorToUser(msj: any) {
    throw new Error(`${msj}`);
}

export function throwErrorIfBadCode(res: any) {
    if (res.respuesta.codigo !== 200) {
      throw new Error(`${res.respuesta.mensaje} (Cod.Error:${res.respuesta.codigo})`);
    }
}

export function throwMensajeToUser(msj: any) {
    throw new Error(`${msj}`);
}


export function getEstadoString(estado: number) : string {
    if (estado == 1) {
        return "Libre";
    } else if (estado == 2) {
        return "Sobreturno Libre";
    } else if (estado == 3) {
        return "Asignado";
    } else if (estado == 4) {
        return "Confirmado";
    } else {
        return estado.toString();
    }

}