import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { catchError, map, mergeMap, switchMap } from 'rxjs/operators';
import { FiltrosTurnos } from '../../../shared/models/request.models';
import * as ErrorActions from '../actions/error.actions';
import * as TurnoActions from '../actions/turno.actions';
import { ServiceService } from '../../services/service.service';


@Injectable()
export class TurnoEffects {

    constructor(
        private actions$: Actions,
        private turnoService: ServiceService,
    ) { }

    asignarTurno$ = createEffect(() =>
    this.actions$.pipe(
        ofType(TurnoActions.ASIGNAR_TURNO),
        mergeMap((payload: any) => this.turnoService.asignarTurno(payload.codigoTurno).pipe(
            map(rtaAsignacion => 
                ({ type: TurnoActions.RTA_ASIGNACION, rtaAsignacion })),
            catchError((error: Error) =>  
              of({ type: TurnoActions.RTA_ASIGNACION, rtaAsignacion: {respuesta : { codigo: 400, mensaje: error.message }}})
            )
        ))
    )
);

    getTurnoByCodigo$ = createEffect(() =>
        this.actions$.pipe(
            ofType(TurnoActions.GET_TURNO),
            mergeMap((payload: any) => this.turnoService.getTurnoByCodigo(payload.codigoTurno).pipe(
                map(turnoPacienteRespuesta =>
                    ({ type: TurnoActions.RTA_TURNO, turnoPacienteRespuesta })),
                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );

    confirmarTurno$ = createEffect(() =>
        this.actions$.pipe(
            ofType(TurnoActions.CONFIRMA_TURNO),
            mergeMap((payload: any) => this.turnoService.confirmarTurno(payload.confirmaTurno).pipe(
                map(rtaConfirmacion =>
                    ({ type: TurnoActions.RTA_CONFIRMACION, rtaConfirmacion })),
                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );


    liberarTurno$ = createEffect(() =>
        this.actions$.pipe(
            ofType(TurnoActions.LIBERA_TURNO),
            mergeMap((payload: any) => this.turnoService.liberarTurno(payload.liberaTurno).pipe(
                map(liberacion =>
                    ({ type: TurnoActions.RTA_LIBERACION, liberacion })),
                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );    

    postPaciente$ = createEffect(() =>
        this.actions$.pipe(
            ofType(TurnoActions.POST_PACIENTE),
            mergeMap((payload: any) => this.turnoService.postPaciente(payload.paciente).pipe(
                map(pacienteSelected =>
                    ({ type: TurnoActions.SET_PACIENTE_SELECTED, pacienteSelected })),

                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );

    getPacientes$ = createEffect(() =>
        this.actions$.pipe(
            ofType(TurnoActions.GET_PACIENTES),
            mergeMap((payload: any) => this.turnoService.getPacientes(payload.filtrosPacientes).pipe(
                map(pacientes => ({ type: TurnoActions.SET_PACIENTES, pacientes })),
                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );

    getPacienteByCodigo$ = createEffect(() =>
        this.actions$.pipe(
            ofType(TurnoActions.GET_PACIENTE_CODIGO),
            mergeMap((payload: any) => this.turnoService.getPacienteByCodigo(payload.filtrosPacientes).pipe(
                map(pacienteSelected => ({ type: TurnoActions.SET_PACIENTE_SELECTED, pacienteSelected })),
                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );
}
