import { Component, Inject, OnInit, ViewChild } from "@angular/core";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from "@angular/material";
import { Store } from "@ngrx/store";
import * as FormSelectors from '../../../../core/store/selectors/form.selectors';
import * as TurnoSelectors from '../../../../core/store/selectors/turno.selectors';
import * as TurnoActions from '../../../../core/store/actions/turno.actions';
import { EspecialidadProfesional, Localidad, ObraSocial, Paciente, Telefono, Turno, TurnoPaciente } from "../../../../shared/models/datos.models";
import { ConfirmacionTurno, DatosFormulario, FiltrosPacientes, LiberacionTurno } from "../../../../shared/models/request.models";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { BusqPacienteDialogComponent } from "../busqPaciente/busqPaciente.component";
import { Observable, of } from "rxjs";
import { throwMensajeToUser } from '../../../../core/utils/service.utils';
import { formatDate } from '@angular/common' 
import { filter, map, startWith, switchMap } from 'rxjs/operators';

export interface DialogData {
  profesional: EspecialidadProfesional;
  turno: Turno;  
  obraSocial: ObraSocial;
}

@Component({
  selector: 'app-asignarTurno-dialog',
  templateUrl: './asignarTurno.component.html',
  styleUrls: ['./asignarTurno.component.css']
})

export class AsignarTurnoDialogComponent implements OnInit {

  @ViewChild('autoLocComplete') autoLocComplete;

  fechaHora: Date;
  profesionalNombre: String;
  obraSocialNombre: String;
  paciente: Paciente;

  rtaAsignado: number;

  // seleccionado en pantalla
  localidad: Localidad;
  // lista del store
  localidades$: Observable<Localidad[]>;
  filteredLocalidades$: Observable<Localidad[]>;
  
  alerta: String;
  alerta2: String;
  error: String;

  dni = new FormControl('', [Validators.required,
  Validators.minLength(7), Validators.maxLength(10), Validators.pattern(/^\d+$/)]);
  apeNom = new FormControl('', [Validators.required, Validators.minLength(5)]);
  nroHC = new FormControl({ value: '', disabled: true });
  edad = new FormControl({ value: '', disabled: true });
  fecNac = new FormControl('', [Validators.required]);
  direccion = new FormControl('');
  localidadFC = new FormControl('', [Validators.required]);
  telefonoArea1 = new FormControl('');
  telefonoNumero1 = new FormControl('');
  telefonoRef1 = new FormControl('');
  telefonoArea3 = new FormControl('');
  telefonoNumero3 = new FormControl('');
  telefonoRef3 = new FormControl('');
  email = new FormControl('', [ Validators.email ]);
  comentario = new FormControl('');
  

  personaFormGroup = new FormGroup({
    dni: this.dni,
    apeNom: this.apeNom,
    fecNac: this.fecNac,
    email: this.email,
  }, {validators: [] })

  turnosAnteriores: TurnoPaciente[];
  displayedColumns: string[] = ['confirmado','fecha', 'os', 'profesional'];

  constructor(
    public dialog: MatDialog,
    private store: Store<{ formulario: DatosFormulario }>,
    public dialogRef: MatDialogRef<AsignarTurnoDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {

    this.localidades$ = this.store.select(FormSelectors.selectAllLocalidades);

    store.select(TurnoSelectors.selectObraSocialSelected).subscribe(
      (os) => {
        this.obraSocialNombre = os?.nombre;
      }
    );

    this.store.select(TurnoSelectors.selectPacienteSelected).subscribe(
      (pe) => {
        this.paciente = pe;
        this.turnosAnteriores = pe?.turnoPaciente;
        if (pe == undefined) {
          pe = new Paciente();
        }
        this.pacienteToView(pe);
      }
    );
    
    this.store.select(TurnoSelectors.selectRtaAsignado).subscribe(
      (rta) => {
        this.alerta2 = undefined;
        this.error = undefined;
        this.rtaAsignado = undefined;
        if (rta != undefined) {
          this.rtaAsignado = rta?.respuesta?.codigo;
          if (rta?.respuesta?.codigo == 200) {  
            //
          } else if (rta?.respuesta?.codigo == 400) {
            this.alerta2 = rta.respuesta?.mensaje;
          } else {
            this.error = rta.respuesta?.mensaje;
          }
        }
      }
    );
        
    this.store.select(TurnoSelectors.selectRespuesta).subscribe(
      (rta) => {
        if (rta != undefined) {
          this.alerta = undefined;
          this.error = undefined;
          if (rta?.respuesta?.codigo == 200) {
            this.dialogRef.close({event:'Confirmo'}); 
            this.store.dispatch(TurnoActions.clear());
            throwMensajeToUser("Turno confirmado con xito!");
          } else if (rta?.respuesta?.codigo == 400) {
            this.alerta = rta.respuesta?.mensaje;
          } else {
            this.error = rta.respuesta?.mensaje;
          }
        }
      }
    );
  }

  ngOnInit() {
    this.fechaHora = this.data.turno.fechaHora;

    this.profesionalNombre = this.data.profesional.nombreApellido;
    this.turnosAnteriores = [];

    this.store.select(TurnoSelectors.alertaCambioEstadoTurnoSeleccionado).subscribe(
      (alerta: string) => {
        if (alerta != undefined) {
          this.alerta = alerta;
        } else {
          this.alerta = undefined;
        }
      }
    ).unsubscribe();    

    this.filteredLocalidades$ = this.localidadFC.valueChanges.pipe(
      startWith<string | Localidad>(''),
      map(value => typeof value === 'string' ? value : value?.nombre),
      switchMap(x => this.filterLoc(x))
    );
    this.localidadFC.valueChanges.subscribe( value => this.cambioLocalidad(value));
  }

  

  onCancelarClick(): void {
    if (this.turnoDisponible()) {
      this.onLiberarThis();
    }
    this.store.dispatch(TurnoActions.clear());
    setTimeout(()=> {
      this.dialogRef.close({event:'Cancel'});
    });
  }

  onConfirmarClick() : void {
    //this.onGuardaPaciente().then(save => {
    //  if (save) {
        this.store.select(TurnoSelectors.reqAsignarTurno).subscribe(
          (confTurno: ConfirmacionTurno) => {
            this.store.dispatch(TurnoActions.confirmarTurno({confirmaTurno: confTurno}));
          }
        ).unsubscribe();  
    //  }
    //});
  }

  onLiberarThis() : void {
    this.onLiberar(this.data.turno.codigo);
  }

  onLiberar(codTurno: number) : void {
    const reqLiberarTurno = new LiberacionTurno();
    this.store.select(TurnoSelectors.selectPacienteSelected).subscribe(
      (ps: Paciente) => {
        reqLiberarTurno.codTurno = codTurno;
        reqLiberarTurno.codPaciente = ps?.codigo;
        this.store.dispatch(TurnoActions.liberarTurno({liberaTurno: reqLiberarTurno}));        
      }
    ).unsubscribe();    
  }

  onLiberarClick(tp: TurnoPaciente) : void {
    this.onLiberar(tp.turno.codigo);
  }

  buscarPacientes() {

    const filtrosPacientes = new FiltrosPacientes();
    filtrosPacientes.dni = this.dni.value;
    filtrosPacientes.apenom = this.apeNom.value;
    
    // this.store.dispatch(TurnoActions.getPacientes({filtrosPacientes}));
    // this.pacientes$ = this.store.select(TurnoSelectors.selectPacientesFiltrados);
    
    const dialogRef = this.dialog.open(BusqPacienteDialogComponent, {
      width: '50%',
      data: {
        filtro: filtrosPacientes
      }
    });
 
    dialogRef.afterClosed().subscribe(result => {
      setTimeout(()=> {
      });
    });
  }

  getDate(dd: String): Date {
    if (dd == undefined || dd.length != 10) {
      return undefined;
    }

    return new Date(
      Number(dd.substring(0,4)),
      Number(dd.substring(6,7)) - 1,
      Number(dd.substring(9,10))
    )
  }

  pacienteToView(pacDto: Paciente) {
    this.nroHC.setValue(pacDto.hc);
    this.dni.setValue(pacDto.dni);
    this.apeNom.setValue(pacDto.nombreApellido);
    //this.fecNac.setValue(pacDto.fechaNacimiento);
    if (pacDto?.fechanacimiento != undefined) {
      this.fecNac.setValue(formatDate(pacDto?.fechanacimiento,'yyyy-MM-dd','en'));
    }
    this.edad.setValue(pacDto?.edad);
    this.direccion.setValue(pacDto?.direccion);
    this.localidadFC.setValue(pacDto?.localidad);

    if (pacDto.telefonoFijo != undefined) {
      this.telefonoArea1.setValue(pacDto?.telefonoFijo?.area);
      this.telefonoNumero1.setValue(pacDto?.telefonoFijo?.numero);
      this.telefonoRef1.setValue(pacDto?.telefonoFijo?.referencia);
    }
    if ( pacDto.celular != undefined) {
      this.telefonoArea3.setValue(pacDto?.celular?.area);
      this.telefonoNumero3.setValue(pacDto?.celular?.numero);
      this.telefonoRef3.setValue(pacDto?.celular?.referencia);
    }
    this.email.setValue(pacDto?.email);
    this.comentario.setValue(pacDto?.comentario);
  }

  viewToPaciente(pacDto: Paciente) {
    var localidad = (this.localidadFC.value != undefined && this.localidadFC.value != '') ? this.localidadFC.value : undefined;

    pacDto.hc = this.nroHC.value;
    pacDto.dni = this.dni.value;
    pacDto.nombreApellido = this.apeNom.value;
    pacDto.fechanacimiento = this.fecNac.value;
    pacDto.fechaNacimiento = this.fecNac.value;
    pacDto.direccion = this.direccion.value;
    pacDto.localidad = localidad,
    pacDto.telefonoFijo = undefined;
    pacDto.celular = undefined;
    if (this.telefonoArea1.value > 0 && this.telefonoNumero1.value > 0) {
      pacDto.telefonoFijo = {
        area: this.telefonoArea1.value,
        numero: this.telefonoNumero1.value,
        referencia: this.telefonoRef1.value
      };
    }
    if (this.telefonoArea3.value > 0 && this.telefonoNumero3.value > 0) {
      pacDto.celular = {
        area: this.telefonoArea3.value,
        numero: this.telefonoNumero3.value,
        referencia: this.telefonoRef3.value
      };
    }
    pacDto.email = this.email.value;
    pacDto.comentario = this.comentario.value;
    return pacDto;
  }

  buscarPaciente(codigo: number) {
    // llamamos a este pq me pierde los turnos al actualizar un paciente existente
    const filtrosPacientes = new FiltrosPacientes();
    filtrosPacientes.codigo = codigo;
    this.store.dispatch(TurnoActions.getPacienteByCodigo({filtrosPacientes}));
    setTimeout(()=> {
      this.store.dispatch(TurnoActions.setPacientes({ pacientes: undefined }));
    }, 200)
  }

  async onGuardaPaciente() {
    return new Promise(save => {
      var pacienteReq = this.viewToPaciente(new Paciente);
      pacienteReq.codigo = this.paciente?.codigo;
      this.store.dispatch(
        TurnoActions.postPaciente({paciente: pacienteReq})
      );
      setTimeout(()=> {
        this.buscarPaciente(this.paciente?.codigo);
      }, 200)
      save(of(true));
    })
  }

  onNuevoPaciente() {
    this.store.dispatch(TurnoActions.setPacienteSelected({pacienteSelected: new Paciente()}));
  }

  hayPaciente() {
    return (this.paciente?.codigo != undefined && this.paciente?.hc && this.paciente?.codigo > 0);
  }

  perCompleta() {
    return this.personaFormGroup.valid;
  }

  filterLoc(value: String): Observable<Localidad[]> {
    if (value != undefined) {
      if (value != undefined && value != "" && !isNaN(Number(value))) {
        return this.localidades$.pipe(
          map(loc => 
            loc.filter(el => 
              el.nombre?.toLowerCase().indexOf(value.toLowerCase()) !== -1 || 
              el.codigo?.toString().indexOf(value.toLowerCase()) === 0)
            .sort((a, b) => {
              if (a.codigo == 0) return -1;
              if (b.codigo == 0) return 1;
              if (a.codigo > b.codigo) return 1;
              if (a.codigo < b.codigo) return -1;
              return 0;
            })            
          )
        );
      }
      
      return this.localidades$.pipe(
        map(loc => 
          loc.filter(el => 
            el.nombre?.toLowerCase().indexOf(value.toLowerCase()) !== -1 || 
            el.codigo?.toString().indexOf(value.toLowerCase()) === 0)
        )
      );
    } else {
      return this.localidades$.pipe(
        map(loc => 
          loc.filter(el => 1 == 1)
        )
      );
    }
  }

  displayLoc(option?: Localidad): string | undefined {
    return option ? `${option.codigo} ${option.nombre}` : undefined;
  }

  cambioLocalidad(value: Localidad) {
    //this.localidad = value;
  }
    
  clearLoc() {
    this.localidadFC.setValue('');
    //this.cambioLocalidad(undefined);
  }

  turnoDisponible() {
    return this.rtaAsignado == 200 && (this.data.turno.estado == 1 || this.data.turno.estado == 2);
  }
}