import { createReducer, on } from '@ngrx/store';
import { Especialidad, EspecialidadProfesional, Licencia, Localidad, ObraSocial } from '../../../shared/models/datos.models';
import { Formulario } from '../../../shared/models/forms.models';
import { TurnoRespuesta } from '../../../shared/models/responses.models';
import * as FormActions from '../actions/form.actions';

export const initialState: 
    Formulario = {
        fechaSelected: new Date(),
        localidades: [],
        obrasSociales: [],
        obraSocialSelected: undefined,
        especialidades: [],
        especialidadSelected: undefined,
        especialidadesProfesionales: [],
        especialidadesProfesionalesFiltradas: [],
        especialidadProfesionalSelected: undefined,
        turnosRta: undefined,
        licencias: [],
};

export function formReducer(state, action) {
    return _formReducer(state, action);
}

const _formReducer = createReducer(
    initialState,

    on(FormActions.clear, () => initialState),

    on(FormActions.setFechaTurno, (state, { fechaSelected }) =>
    _setFechaSelected(state, fechaSelected)),

    on(FormActions.setFechaTurno, (state, { fechaSelected }) =>
        _setFechaSelected(state, fechaSelected)),

    on(FormActions.setLocalidades, (state, { localidades }) =>
        _setLocalidades(state, localidades)),

    on(FormActions.setObraSociales, (state, { obrasSociales }) =>
        _setObraSociales(state, obrasSociales)),

    on(FormActions.setObraSocialSelected, (state, { obraSocialSelected }) =>
        _setObraSocialSelected(state, obraSocialSelected)),

    on(FormActions.setAlertaObraSocial, (state, { obraSocial }) =>
        _setAlertaObraSocial(state, obraSocial)),

    on(FormActions.setEspecialidades, (state, { especialidades }) =>
        _setEspecialidades(state, especialidades)),

    on(FormActions.setEspecialidadSelected, (state, { especialidadSelected }) =>
        _setEspecialidadSelected(state, especialidadSelected)),

    on(FormActions.setEspecialidadesProfesionales, (state, { especialidadesProfesionales }) =>
        _setEspecialidadesProfesionales(state, especialidadesProfesionales)),

    on(FormActions.setLicencias, (state, { licencias }) =>
        _setLicencias(state, licencias)),

    on(FormActions.setEspecialidadesProfesionalesFiltradas, (state, { especialidadesProfesionalesFiltradas }) =>
        _setEspecialidadesProfesionalesFiltradas(state, especialidadesProfesionalesFiltradas)),

    on(FormActions.setEspecialidadProfesionalSelected, (state, { especialidadProfesionalSelected }) =>
        _setEspecialidadProfesionalSelected(state, especialidadProfesionalSelected)),

    on(FormActions.setTurnos, (state, { turnosRta }) =>
        _setTurnos(state, turnosRta)),

);

const _setFechaSelected = (state: Formulario, fechaSelected: Date) => {
    const stateNew = { ...state };
    stateNew.fechaSelected = fechaSelected;
    return stateNew;
};

const _setEspecialidadSelected = (state: Formulario, especialidadSelected: Especialidad) => {
    const stateNew = { ...state };
    stateNew.especialidadSelected = especialidadSelected;
    return stateNew;
};

const _setEspecialidades = (state: Formulario, especialidades: Especialidad[]) => {
    const stateNew = { ...state };
    stateNew.especialidades = especialidades;
    return stateNew;
};

const _setEspecialidadProfesionalSelected = (state: Formulario, especialidadProfesionalSelected: EspecialidadProfesional) => {
    const stateNew = { ...state };
    if (especialidadProfesionalSelected == undefined) {
        stateNew.especialidadProfesionalSelected = undefined;
    } else {
        stateNew.especialidadProfesionalSelected = 
            state.especialidadesProfesionales
                .filter(e => e.codigo == especialidadProfesionalSelected.codigo)[0];
    }
    return stateNew;
};

const _setEspecialidadesProfesionales = (state: Formulario, especialidadesProfesionales: EspecialidadProfesional[]) => {
    const stateNew = { ...state };
    stateNew.especialidadesProfesionales = especialidadesProfesionales;
    return stateNew;
};

const _setEspecialidadesProfesionalesFiltradas = (state: Formulario, especialidadesProfesionalesFiltradas: EspecialidadProfesional[]) => {
    const stateNew = { ...state };
    stateNew.especialidadesProfesionalesFiltradas = especialidadesProfesionalesFiltradas;
    return stateNew;
};

const _setLicencias = (state: Formulario, licencias: Licencia[]) => {
    const stateNew = { ...state };
    stateNew.licencias = licencias;
    return stateNew;
};

const _setLocalidades = (state: Formulario, localidades: Localidad[]): Formulario => {
    const stateNew = { ...state };
    stateNew.localidades = localidades;
    return stateNew;
};

const _setObraSociales = (state: Formulario, obraSociales: ObraSocial[]): Formulario => {
    const stateNew = { ...state };
    stateNew.obrasSociales = obraSociales;
    return stateNew;
};

const _setAlertaObraSocial = (state: Formulario, obraSocial: ObraSocial): Formulario => {
    const stateNew = { ...state };
    stateNew.obraSocialSelected = obraSocial;
    //stateNew.obrasSociales.filter(e => e.codigo == obraSocial.codigo)[0].alertas = obraSocial.alertas;
    return stateNew;
};

const _setObraSocialSelected = (state: Formulario, obraSocialSelected: ObraSocial): Formulario => {
    const stateNew = { ...state };
    stateNew.obraSocialSelected = obraSocialSelected;
    //stateNew.planSelected = undefined;
    return stateNew;
};

const _setTurnos = (state: Formulario, turnosRta: TurnoRespuesta): Formulario => {
    const stateNew = { ...state };
    stateNew.turnosRta = turnosRta;
    return stateNew;
};





