import { registerLocaleData } from '@angular/common';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import localeEs from '@angular/common/locales/es-AR';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { DateAdapter, MatAutocompleteModule, MatButtonModule, MatButtonToggleModule, MatCheckboxModule, MatChipsModule, MatDialogModule, MatFormFieldModule, MatIconModule, MatInputModule, MatProgressSpinnerModule, MatRadioModule, MatSelectModule, MatSlideToggle, MatSlideToggleModule, MatTableModule, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { EffectsModule } from '@ngrx/effects';
import { StoreRouterConnectingModule } from '@ngrx/router-store';
import { StoreModule } from '@ngrx/store';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { environment } from '../environments/environment';
import { FormEffects } from './../app/core/store/effects/form.effects';
import { TurnoEffects } from './../app/core/store/effects/turno.effects';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { CoreModule } from './core/core.module';
import { HeaderComponent } from './core/header/header.component';
import { HttpErrorInterceptor } from './core/interceptor/error.interceptor';
import { TokenInterceptor } from './core/interceptor/token.interceptor';
import { ContextEffects } from './core/store/effects/context.effects';
import { ErrorEffects } from './core/store/effects/error.effects';
import { contextoReducer } from './core/store/reducers/contexto.reducers';
import { errorReducer } from './core/store/reducers/error.reducers';
import { formReducer } from './core/store/reducers/form.reducers';
import { turnoReducer } from './core/store/reducers/turno.reducers';
import { storageMetaReducer } from './core/store/reducers/storage.metareducer';
import { AsignarTurnoDialogComponent } from './modules/home/components/asignarTurno/asignarTurno.component';
import { BusqProfesionalDialogComponent } from './modules/home/components/busqProfesional/busqProfesional.component';
import { LicenciasDialogComponent } from './modules/home/components/licencias/licencias.component';
import { CustomDateAdapter, MY_DATE_FORMATS } from './shared/adapters/common';
import { SharedModule } from './shared/shared.module';
import { BusqPacienteDialogComponent } from './modules/home/components/busqPaciente/busqPaciente.component';
registerLocaleData(localeEs);

@NgModule({ 
  declarations: [
    AppComponent, 
    LicenciasDialogComponent,
    BusqProfesionalDialogComponent, 
    AsignarTurnoDialogComponent,
    BusqPacienteDialogComponent, 
    HeaderComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    MatButtonModule,
    MatButtonToggleModule,
    AppRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    MatTableModule,
    MatInputModule,
    MatAutocompleteModule,
    MatIconModule,
    MatSelectModule,
    MatCheckboxModule,
    MatSlideToggleModule,
    MatRadioModule,
    MatFormFieldModule,
    MatChipsModule,
    MatDialogModule,
    SharedModule,
    StoreModule.forRoot({
      // router: routerReducer,
      contexto: contextoReducer,
      formulario: formReducer,
      formularioTurno: turnoReducer,
      error: errorReducer,
    },
    { 
      metaReducers: [storageMetaReducer] 
    }
   ),
    StoreRouterConnectingModule.forRoot(),
    StoreDevtoolsModule.instrument({
      maxAge: 25, // Retains last 25 states
      logOnly: environment.production, // Restrict extension to log-only mode
    }),
    EffectsModule.forRoot([
      FormEffects,
      TurnoEffects,
      ErrorEffects,
      ContextEffects,
    ]),
    BrowserAnimationsModule,
    MatProgressSpinnerModule
  ],
  exports: [
    CoreModule,
    SharedModule,
    MatProgressSpinnerModule,
    MatRadioModule
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: HttpErrorInterceptor,
      multi: true
    },
    { provide: MAT_DATE_LOCALE, useValue: 'es-AR'},
    { provide: DateAdapter, useClass: CustomDateAdapter },
    { provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { 
}
