import { createFeatureSelector, createSelector } from '@ngrx/store';
import { FormularioTurno } from '../../../shared/models/forms.models';
import { ConfirmacionTurno, FiltrosPacientes } from '../../../shared/models/request.models';
import { getEstadoString } from '../../utils/service.utils';

export const selectFormularioTurno = createFeatureSelector<FormularioTurno>('formularioTurno');

// seleccion

// obras sociales
export const selectObraSocialSelected = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => formularioTurno.obraSocialSelected
);
// turno
export const selectTurnoSelected = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => formularioTurno.turnoSelected
);

export const selectTurnoRecuperado = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => formularioTurno.turnoRecuperado
);

export const alertaCambioEstadoTurnoSeleccionado = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => {
        if (formularioTurno?.turnoRecuperado?.turno?.estado != formularioTurno?.turnoSelected?.estado) {
            var estadoIni = getEstadoString(formularioTurno.turnoSelected.estado);
            var estadoNvo = getEstadoString(formularioTurno.turnoRecuperado.turno.estado);
            return `El turno elegido cambio su estado de ${ estadoIni } a ${ estadoNvo }`;
        }
        return undefined;
    }
);

export const reqAsignarTurno = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => {
        const request = new ConfirmacionTurno();
        if (formularioTurno.turnoSelected != undefined) {
            request.codTurno = formularioTurno.turnoSelected.codigo;
        }
        if (formularioTurno.pacienteSelected != undefined) {
            request.codPaciente = formularioTurno.pacienteSelected.codigo;
        }
        if (formularioTurno.obraSocialSelected != undefined) {
            request.codObraSocial = formularioTurno.obraSocialSelected.codigo;
        }
        return request;
    }
);

// pacientes

export const selectPacientesFiltrados = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => formularioTurno.pacientes
);

// paciente

export const selectFiltroPacientes = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => {
        const request = new FiltrosPacientes();
        if (formularioTurno.datosPaciente != undefined && 
                formularioTurno.datosPaciente.dni != undefined) {
            request.dni = formularioTurno.datosPaciente.dni.toString();
        }
        if (formularioTurno.datosPaciente != undefined && 
            formularioTurno.datosPaciente.nombreApellido != undefined) {
            request.apenom = formularioTurno.datosPaciente.nombreApellido.trim();
        }
    return request;
    }
);

export const selectPacienteSelected = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => formularioTurno.pacienteSelected
);

export const selectRtaAsignado = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => formularioTurno.rtaAsignacion
);

export const selectRespuesta = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => formularioTurno.rtaConfirmacion
);

