import { Component, Inject, OnInit, ViewChild } from "@angular/core";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material";
import { Store } from "@ngrx/store";
import * as ContextSelectors from '../../../../core/store/selectors/contexto.selectors';
import * as FormSelectors from '../../../../core/store/selectors/form.selectors';
import * as FormActions from '../../../../core/store/actions/form.actions';
import { Especialidad, EspecialidadProfesional, ObraSocial } from "../../../../shared/models/datos.models";
import { DatosFormulario, FiltrosProfesionales } from "../../../../shared/models/request.models";
import { FormControl, Validators } from "@angular/forms";
import { Observable } from "rxjs";
import { filter, map, startWith, switchMap } from "rxjs/operators";
  
export interface DialogData {
  tipoBusqueda: number;
  profesional: EspecialidadProfesional;
}

@Component({
  selector: 'app-busqProfesional-dialog',
  templateUrl: './busqProfesional.component.html',
  styleUrls: ['./busqProfesional.component.css']
})

export class BusqProfesionalDialogComponent implements OnInit {

  @ViewChild('autoEspComplete') autoEspComplete;
  @ViewChild('autoObraComplete') autoObraComplete;
  
  // filtros
  especialidadFC = new FormControl('');
  obraSocialFC = new FormControl('', [Validators.required]);

  // elegidos en la pantalla
  especialidad: Especialidad;
  obraSocial: ObraSocial;

  // os del store
  filteredObrasSociales$: Observable<ObraSocial[]>;
  obrasSociales$: Observable<ObraSocial[]>;

  // especialidad del store
  filteredEspecialidades$: Observable<Especialidad[]>;
  especialidades$: Observable<Especialidad[]>;

  // profesioanles     
  resultados: EspecialidadProfesional[];
  displayedColumnsBusq1: string[] = ['codigo','nombreApellido', 'sinOS', 'AM', 'PM' ];

  constructor(
    private store: Store<{ formulario: DatosFormulario }>,
    public dialogRef: MatDialogRef<BusqProfesionalDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {

    this.obrasSociales$ = store.select(FormSelectors.selectAllObrasSociales);
    this.especialidades$ = store.select(FormSelectors.selectAllEspecialidades);

    store.select(FormSelectors.selectProfesionalesBusquedaA).subscribe(
      (profesionales) => {
        this.resultados = profesionales;
      }
    );
  }

  ngOnInit() {
    this.store.dispatch(FormActions.setEspecialidadesProfesionalesFiltradas(
      { especialidadesProfesionalesFiltradas: undefined }
    ));

    this.store.select(ContextSelectors.getToken).pipe(
      filter(token => (token != undefined || token != null))
      ).subscribe(
        () => {
          this.filteredObrasSociales$ = this.obraSocialFC.valueChanges.pipe(
            startWith<string | ObraSocial>(''),
            map(value => typeof value === 'string' ? value : value.nombre),
            switchMap(x => this.filterOs(x))
          );
          this.obraSocialFC.valueChanges.subscribe( value => this.cambioObraSocial(value));

          this.filteredEspecialidades$ = this.especialidadFC.valueChanges.pipe(
            startWith<string | Especialidad>(''),
            map(value => typeof value === 'string' ? value : value.nombre),
            switchMap(x => this.filterEsp(x))
          );
          this.especialidadFC.valueChanges.subscribe( value => this.cambioEspecialidad(value));

        }
      ).unsubscribe();
  }

  filterOs(value: String): Observable<ObraSocial[]> {
    const filterValue = value.toLowerCase();

    if (value != "" &&  !isNaN(Number(value))) {
      return this.obrasSociales$.pipe(
        map(os => 
          os.filter(el => 
            el.nombre?.toLowerCase().indexOf(filterValue) !== -1 || 
            el.codigo?.toString().indexOf(filterValue) === 0)
          .sort((a, b) => {
            if (a.codigo == 0) return -1;
            if (b.codigo == 0) return 1;
            if (a.codigo > b.codigo) return 1;
            if (a.codigo < b.codigo) return -1;
            return 0;
          })            
        )
      );
    }
    
    return this.obrasSociales$.pipe(
      map(os => 
        os.filter(el => 
          el.nombre?.toLowerCase().indexOf(filterValue) !== -1 || 
          el.codigo?.toString().indexOf(filterValue) === 0)
      )
    );
  }

  filterEsp(value: String): Observable<Especialidad[]> {
    const filterValue = value.toLowerCase();

    if (value != "" &&  !isNaN(Number(value))) {
      return this.especialidades$.pipe(
        map(esp => 
          esp.filter(esp => 
            esp.nombre?.toLowerCase().indexOf(filterValue) !== -1 || 
            esp.codigo?.toString().indexOf(filterValue) !== -1)
          .sort((a, b) => {
              if (a.codigo == 0) return -1;
              if (b.codigo == 0) return 1;
              if (a.codigo > b.codigo) return 1;
              if (a.codigo < b.codigo) return -1;
              return 0;
          })             
        )
        
      );      
    }

    return this.especialidades$.pipe(
      map(esp => 
        esp.filter(esp => 
          esp.nombre?.toLowerCase().indexOf(filterValue) !== -1 || 
          esp.codigo?.toString().indexOf(filterValue) !== -1)
      )
    );
  }

  cambioObraSocial(value) {
    this.store.dispatch(FormActions.setObraSocialSelected(
      { obraSocialSelected: value }
    ));
  }

  cambioEspecialidad(value) {
    this.store.dispatch(FormActions.setEspecialidadSelected(
      { especialidadSelected: value }
    ));
  }

  onCancelarClick(): void {
    this.data.profesional = new EspecialidadProfesional;

    this.store.dispatch(FormActions.setEspecialidadProfesionalSelected(
      { especialidadProfesionalSelected: undefined }
    ));
    setTimeout(()=> {
      this.dialogRef.close();
    }, 200)
  }

  onClickProf(seleccion: EspecialidadProfesional) {
    this.data.profesional = seleccion;
    this.store.dispatch(FormActions.setEspecialidadProfesionalSelected(
      { especialidadProfesionalSelected: seleccion }
    ));
    this.dialogRef.close();
  }

  buscarProfesionales() {
    this.store.select(FormSelectors.selectBusquedaProfesionales).subscribe(
      (filtrosProfesionales: FiltrosProfesionales) => {
        this.store.dispatch(FormActions.getEspecialidadesProfesionalesFiltradas({filtrosProfesionales}));
      }
    ).unsubscribe();  
  }

  // evento emter 
  onEnterE2(evt: any, field: string){
    if (evt && evt.key === "Enter") {
      this.onEnterE(evt, field);
    }
  }
  onEnterE(evt: any, field: string){
    setTimeout(()=> {

    })
  }

  // como resuelve el display en los combos

  displayOS(option?: ObraSocial): string | undefined {
    return option ? `${option.codigo} ${option.nombre}` : undefined;
  }

  displayEsp(option?: Especialidad): string | undefined {
    return option ? `${option.codigo} ${option.nombre}` : undefined;
  }

  // crucecitas para limpiar combos
  clearOS() {
    this.obraSocialFC.setValue('');
  }

  clearEspecialidad() {
    this.especialidadFC.setValue('');
  }

  isValid() {
    let result = false;
    if (this.obraSocialFC.valid && this.especialidadFC.valid) {
      result = true;
    }
    return result;
  }
}