import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { delay, map } from 'rxjs/operators';
// tslint:disable-next-line: max-line-length

import { Especialidad, EspecialidadProfesional, EspecialidadProfesionalBusq, Licencia, Localidad, ObraSocial, Paciente, Telefono, Turno, TurnoPaciente } from '../../shared/models/datos.models';
import { FiltrosPacientes, FiltrosTurnos, Login, ConfirmacionTurno, LiberacionTurno } from '../../shared/models/request.models';
import { AlertasObraSocialRespuesta, EspecialidadProfesionalBusqRespuesta, EspecialidadProfesionalRespuesta, EspecialidadRespuesta, LicenciaRespuesta, LocalidadRespuesta, LoginRespuesta, ObraSocialRespuesta, PacienteRespuesta, PacientesRespuesta, Respuesta, RespuestaDTO, TurnoPacienteRespuesta, TurnoRespuesta } from '../../shared/models/responses.models';
import * as Mock from '../mocks/mocks';
import { getWsFromMock, throwErrorIfBadCode, throwErrorToUser } from '../utils/service.utils';
import { environment } from './../../../environments/environment';

@Injectable()
export class ServiceService {

  constructor (
    protected http: HttpClient
  ) { }

  useMockups = environment.mockups;
  endpoint = environment.endpoint;
  endpointC = `${this.endpoint}/Consext` ;
  endpointG = `${this.endpoint}/Gestion` ;
  endpointA = `${this.endpoint}/Auth` ;


  login(usuario: Login): Observable<any> {
    if (this.useMockups) {
      return getWsFromMock(Mock.tokenMock);
    } else {

      return this.http.post<LoginRespuesta>(`${this.endpointA}/Login`, usuario)
        .pipe(map(
          (res: LoginRespuesta) => {
            if (res.token == undefined || res.token.length == 0) {
              throwErrorToUser(`Por favor intente ms tarde.`);
            }
            return res.token;
          }
      ));
    }
  }

  getEspecialidades(): Observable<Especialidad[]> {
    if (this.useMockups) {
      return getWsFromMock(Mock.especialidadesMocks);
    } else {

      return this.http.get<EspecialidadRespuesta>(`${this.endpointC}/especialidad`)
        .pipe(map(
          (res: EspecialidadRespuesta) => {
            throwErrorIfBadCode(res);

            res.especialidad.forEach(element => {
              element.nombre = element.nombre.trim();
            });

            return res.especialidad.sort((a, b) => {
              if (a.nombre > b.nombre) return 1;
              if (a.nombre < b.nombre) return -1;
              return 0;
            });
          }
      ));
    }
  }

  getObrasSociales(): Observable<ObraSocial[]> {
    if (this.useMockups) {
      return getWsFromMock(Mock.obrasSocialesMocks);
    } else {

      return this.http.get<ObraSocialRespuesta>(`${this.endpointC}/planesObrasSociales/`)
        .pipe(map(
          (res: ObraSocialRespuesta) => {
            throwErrorIfBadCode(res);
            
            res.obraSocial.forEach(element => {
              if (element.nombre != undefined) {
                element.nombre = element.nombre.trim();
              }
              if (element.plan != undefined) {
                element.plan.forEach(elementE => {
                  elementE.nombre = elementE.nombre.trim();
                });
              }
            });

            return res.obraSocial
              .filter(x => x.nombre != undefined && x.nombre?.trim().length > 0)
              .sort((a, b) => {
                if (a.nombre > b.nombre) return 1;
                if (a.nombre < b.nombre) return -1;
                return 0;
            });
          }
      ));
    }
  }

  getObraSocial(codOS: string): Observable<ObraSocial> {
    if (this.useMockups) {
      return getWsFromMock(Mock.obraSocialMock1);
    } else {

      return this.http.get<AlertasObraSocialRespuesta>(`${this.endpointC}/planesObrasSociales/${codOS}`)
        .pipe(map(
          (res: AlertasObraSocialRespuesta) => {
            throwErrorIfBadCode(res);
            res.obraSocial.nombre = res.obraSocial.nombre.trim();
            if (res.obraSocial.alertas == undefined) {
              res.obraSocial.alertas = [];
            } else {
              res.obraSocial.alertas.forEach(element => {
                element = element.trim();
              });
            }
            return res.obraSocial;
          }
      ));
    }
  }

  getEspecialidadesProfesionales(): Observable<EspecialidadProfesional[]> {
    if (this.useMockups) {
      return getWsFromMock(Mock.especialidadProfMock);
    } else {

      return this.http.get<EspecialidadProfesionalRespuesta>(`${this.endpointC}/especialidadesProfesional`)
        .pipe(map(
          (res: EspecialidadProfesionalRespuesta) => {
            throwErrorIfBadCode(res);
            res.especialidadProfesional.forEach(element => {
              if (element.nombreApellido != undefined) {
                element.nombreApellido = element.nombreApellido.trim();
              }
              if (element.especialidad != undefined) {
                element.especialidad.forEach(esp => {
                  esp.nombre = esp.nombre.trim();
                })
              }
            });

            var busqProf: EspecialidadProfesional = {
              codigo:0, 
              nombreApellido: "BSQUEDA AVANZADA",
              especialidad: undefined,
              horario: undefined,
              alertas: undefined,
              turnoParticular: undefined,
              turnoAM: undefined,
              turnoPM: undefined             
            };

            res.especialidadProfesional.push(busqProf);

            return res.especialidadProfesional.sort((a, b) => {
              if (a.codigo == 0) return -1;
              if (b.codigo == 0) return 1;
              if (a.nombreApellido > b.nombreApellido) return 1;
              if (a.nombreApellido < b.nombreApellido) return -1;
              return 0;
            });

          }
      ));
    }
  }

 
  getEspecialidadesProfesionalesFiltradas(os: string, esp:string): Observable<EspecialidadProfesionalBusq[]> {
    if (this.useMockups) {
      return getWsFromMock(Mock.especialidadProfFiltradasMock);
    } else {

      // GET /Consext/especialidadesProfesional/busqueda?os={codOS}&esp={codEsp}
      var endpointB = `${this.endpointC}/especialidadesProfesional/busqueda?os=${os}&esp=${esp}`;
      return this.http.get<EspecialidadProfesionalBusqRespuesta>(endpointB).pipe(map(
        (res: EspecialidadProfesionalBusqRespuesta) => {
          throwErrorIfBadCode(res);
          res.especialidadProfesional.forEach(element => {
            element.nombreApellido = element.nombreApellido?.trim();
            element.especialidad.nombre = element.especialidad.nombre?.trim();
            //element.turnoParticular
            //element.turnoAM
            //element.turnoPM
          });

          return res.especialidadProfesional;
        }
      ));
    }
  }
  
  getLicencias(codProf: string): Observable<Licencia[]> {
    if (this.useMockups) {
      return getWsFromMock(Mock.licenciasMock);
    } else {

      return this.http.get<LicenciaRespuesta>(`${this.endpointC}/licencias/${codProf}`)
      .pipe(map(
        (res: LicenciaRespuesta) => {
          throwErrorIfBadCode(res);

          res.licencia.forEach(element => {
            element.motivo = element.motivo.trim();
          });

          return res.licencia;
        }
      ));
    }
  }

  getTurnosFiltrados(filtrosTurnos: FiltrosTurnos): Observable<TurnoRespuesta> {
    if (this.useMockups) {
      return filtrosTurnos.os === '12345' ? getWsFromMock(Mock.turnoRespuestaLic) : getWsFromMock(Mock.turnoRespuesta);
    } else {
      var endpoint = undefined;
      if (filtrosTurnos.os != undefined && filtrosTurnos.ep != undefined && filtrosTurnos.fecha != undefined) {
        endpoint = `${this.endpointC}/turnos?codOS=${filtrosTurnos.os}&codProf=${filtrosTurnos.ep}&fecha=${filtrosTurnos.fecha}`;
      }
      if (endpoint != undefined) {
        return this.http.get<TurnoRespuesta>(endpoint)
          .pipe(map(
            (res: TurnoRespuesta) => {
              throwErrorIfBadCode(res);
              if (res.licencia != undefined && res.licencia.motivo != undefined) {
                res.licencia.motivo = res.licencia.motivo.trim();
              }
              return res;
            }
        ));
      } else {
        var emptyRta : TurnoRespuesta = {
          respuesta: undefined,
          turno: undefined,
          licencia: undefined
        }
        return of(emptyRta).pipe(delay(100));
      }
    }
  }
  
  postPaciente(paciente: Paciente) : Observable<Paciente> {
    if (this.useMockups) {
      return getWsFromMock(Mock.pacientesList.paciente);

    } else {
      var endpoint =  `${this.endpointC}/pacientes/`;
      if (paciente.codigo != undefined) {
        endpoint =  `${this.endpointC}/pacientes/${paciente.codigo}`;
      }

      return this.http.post<PacienteRespuesta>(endpoint, paciente)
        .pipe(map(
          (res: PacienteRespuesta) => {
            throwErrorIfBadCode(res);
            return res.paciente;
          }
        ));
    }
  }

  getPacientes(filtrosPacientes: FiltrosPacientes): Observable<Paciente[]> {
    if (filtrosPacientes == undefined || 
      ( (filtrosPacientes.apenom == undefined || filtrosPacientes.apenom.trim().length == 0) && 
        (filtrosPacientes.dni == undefined || filtrosPacientes.dni.toString().trim().length == 0)
      )
    ) {
        throw new Error(`Ingrese algun criterio de bsqueda`);
    }

    if (this.useMockups) {
      return getWsFromMock(Mock.pacientesList.paciente);

    } else {
      var endpoint =  `${this.endpointC}/pacientes/busqueda/`;
      
      if (filtrosPacientes.dni != undefined && filtrosPacientes.dni.toString().trim().length > 0) {
        endpoint = `${endpoint}/${filtrosPacientes.dni.toString().trim()}`;
        if (filtrosPacientes.apenom != undefined && filtrosPacientes.apenom.trim().length > 0) {
          endpoint = `${endpoint}?apenom=${filtrosPacientes.apenom.trim()}`;
        } 

      } else if (filtrosPacientes.apenom != undefined && filtrosPacientes.apenom.trim().length > 0) {
        endpoint = `${endpoint}/${filtrosPacientes.apenom.trim()}`;
      } 

      return this.http.get<PacientesRespuesta>(endpoint).pipe(
        map((res: PacientesRespuesta) => {
          throwErrorIfBadCode(res);
          return res.paciente;
        })
      );
    }
  }

  getPacienteByCodigo(filtrosPacientes: FiltrosPacientes): Observable<Paciente> {
    if (filtrosPacientes == undefined || filtrosPacientes.codigo == undefined) {
        throw new Error(`Ingrese algun criterio de bsqueda`);
    } else if (filtrosPacientes.codigo < 0) {
      var emptyRta : Paciente = {
        codigo: undefined,
        hc: undefined,
        dni: undefined,
        fechaNacimiento: undefined,
        fechanacimiento: undefined,
        edad: undefined,
        email: undefined,
        comentario: undefined,
        turnoPaciente: [],
        nombreApellido: undefined,
        direccion: undefined,
        localidad: undefined,
        telefonoFijo: undefined,
        celular: undefined
      }
      return of(emptyRta).pipe(delay(100));
  }

    if (this.useMockups) {
      return getWsFromMock(Mock.paciente1);
    } else {

      return this.http.get<PacienteRespuesta>(`${this.endpointC}/pacientes/${filtrosPacientes.codigo}`).pipe(
        map((res: PacienteRespuesta) => {
          throwErrorIfBadCode(res);
          return res.paciente;
        })
      );
    }
  }

  asignarTurno(codigoTurno: number): Observable<Respuesta> {
    if (this.useMockups) {
      return getWsFromMock(Mock.rtaError);
    } else {

      return this.http.post<Respuesta>(`${this.endpointC}/turnos/${codigoTurno}/asignar`, codigoTurno)
      .pipe(map(
        (res: Respuesta) => {
          throwErrorIfBadCode(res);
          return res;
        }
      ));
    }
  }

  confirmarTurno(confirmaTurno: ConfirmacionTurno): Observable<Respuesta> {
    if (this.useMockups) {
      return getWsFromMock(Mock.rtaOk);
    } else {
      
      return this.http.post<Respuesta>(`${this.endpointC}/turnos/${confirmaTurno?.codTurno}/confirmar`, confirmaTurno)
      .pipe(map(
        (rtaConfirmacion: Respuesta) => {
          throwErrorIfBadCode(rtaConfirmacion);
          return rtaConfirmacion;
        }
      ));
    }
  }

  liberarTurno(liberaTurno: LiberacionTurno): Observable<RespuestaDTO> {
    if (this.useMockups) {
      return getWsFromMock(Mock.rtaError);
    } else {

      return this.http.post<Respuesta>(`${this.endpointC}/turnos/${liberaTurno?.codTurno}/liberar`, liberaTurno)
      .pipe(map(
        (res: Respuesta) => {
          throwErrorIfBadCode(res);
          return res.respuesta;
        }
      ));
    }
  }

  getTurnoByCodigo(codigoTurno: number): Observable<TurnoPacienteRespuesta> {
    if (this.useMockups) {
      return codigoTurno >= 40 ? getWsFromMock(Mock.turnoPacienteRtaO) : getWsFromMock(Mock.turnoPacienteRtaL);
    } else {
      return this.http.get<TurnoPacienteRespuesta>(`${this.endpointC}/turnos/codTurno=${codigoTurno}`)
        .pipe(map(
          (res: TurnoPacienteRespuesta) => {
            throwErrorIfBadCode(res);
            return res;
          }
      ));
    }
  }

  getLocalidades(): Observable<Localidad[]> {
    if (this.useMockups) {
      return undefined;
    } else {
      return this.http.get<LocalidadRespuesta>(`${this.endpointC}/localidad`)
        .pipe(map(
          (res: LocalidadRespuesta) => {
            throwErrorIfBadCode(res);
            return res.localidad;
          }
      ));
    }
  }
}

