// login
export class Contexto {
    token: string;
}

// obra social

export class ObraSocial {
    codigo: number;
    nombre: string;
    plan: Plan[];
    alertas: string[];
}

export class Plan {
    codigo: number;
    nombre: string;
}

// centro atencion
export class CentroAtencion {
    codigo: number;
    nombre: string;
}

// especialidad

export class Especialidad {
    codigo: number;
    nombre: string;
}

// especialidadProfesional

export class Rango {
    horaDesde: string;
    horaHasta: string;
}

export class Horario {
    diaSemana: string;
    rangoAM: Rango;
    rangoPM: Rango;
}

export class EspecialidadProfesional {
    codigo: number; // VER: es en realidad el cod del profesional con muchas especialidades
    nombreApellido: string;
    especialidad: Especialidad[];
    horario: Horario[];
    alertas: string[];
    turnoParticular: Turno;
    turnoAM: Turno;
    turnoPM: Turno;
}

export class EspecialidadProfesionalBusq {
    codigo: number; 
    nombreApellido: string;
    especialidad: Especialidad;
    horario: Horario[];
    alertas: string[];
    turnoParticular: Turno;
    turnoAM: Turno;
    turnoPM: Turno;
}

// licencias
export class Licencia {
    fechaDesde: Date;
    fechaHasta: Date;
    motivo: string;
}

// turnos
export class Turno {
    codigo: number;
    fechaHora: Date;
    estado: number;
}

// paciente
export class Localidad {
    codigo: number;
    nombre: string;
}
export class Telefono {
    area: number;
    numero: number;
    referencia: string;
}

export class Paciente {
    codigo: number;
    hc: number;
    dni: number;
    fechanacimiento: string;
    fechaNacimiento: string;
    edad: string;
    email: string;
    comentario: string;
    turnoPaciente: TurnoPaciente[];
    nombreApellido: string;
    direccion: string;
    localidad: Localidad;
    celular: Telefono;
    telefonoFijo: Telefono;
}

export class TurnoPaciente {
    turno: Turno;
    confirmado: boolean;
    fechaHora: string;
    obraSocial: ObraSocial;
    especialidadProfesional: EspecialidadProfesional;
}

export class TurnoAsignado {
    codigoTurno: number;
    codigoPaciente: number;
    obraSocial: ObraSocial;
}

