import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import * as ContextoActions from '../../core/store/actions/contexto.actions';
import * as FormActions from '../../core/store/actions/form.actions';
import * as TurnoActions from '../../core/store/actions/turno.actions';
import * as ContextSelectors from '../store/selectors/contexto.selectors';
import { Contexto } from '../../shared/models/datos.models';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  token$: Observable<String>;

  constructor(
    private store: Store<{ contexto: Contexto }>,
    private router: Router
  ) {
  }

  ngOnInit() {
    this.token$  = this.store.select(ContextSelectors.getToken);
  }

  logout() {
    this.store.dispatch(ContextoActions.setToken({ token: undefined }));
    this.store.dispatch(FormActions.clear());
    this.store.dispatch(TurnoActions.clear());
    this.router.navigate(['/home']);
  }
}
