import {Injectable} from '@angular/core';
import {HttpHandler, HttpRequest, HttpInterceptor} from '@angular/common/http';
import {throwError} from 'rxjs';
import {catchError} from 'rxjs/internal/operators';
import {ServiceService} from '../services/service.service';
import {throwErrorToUser} from '../utils/service.utils';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Contexto } from '../../shared/models/datos.models';
import * as ContextoActions from '../../core/store/actions/contexto.actions';
import * as FormActions from '../../core/store/actions/form.actions';
import * as TurnoActions from '../../core/store/actions/turno.actions';


@Injectable({
  providedIn: 'root'
})
export class HttpErrorInterceptor implements HttpInterceptor {

  constructor(
    private store: Store<{ contexto: Contexto }>,
    private errorService: ServiceService,
    private router: Router
  ) { }

  intercept(req: HttpRequest<any>, next: HttpHandler) {
    return next.handle(req).pipe(
      catchError(error => {
        let errorMessage = '';

        if (error instanceof ErrorEvent) {
          // client-side error
          //errorMessage = `Client-side error: ${error.error.message}`;
          console.error(`Client-side error: ${error.error.message}`);
          errorMessage = `Se ha producido un error. Por favor reintente ms tarde`;
        } else {
          // backend error
          if (error.error != undefined && error.error.mensaje) {
            errorMessage = `${error.error.mensaje}`;
            console.error(`Server-side error: ${error.error.mensaje} (CodError: ${error.error.codigo})`);
          } else {
            console.error(`WS error ${error.status}: ${error.message}`);
            if (error.status === 401) {
              errorMessage = `Usuario o contrasea invlidos`;
              this.store.dispatch(ContextoActions.setToken({ token: undefined }));
              this.store.dispatch(FormActions.clear());
              this.store.dispatch(TurnoActions.clear());
              this.router.navigate(['/home']);
            } if (error.status === 404) {
              //errorMessage = `Error ${error.status}: ${error.message}`;
              errorMessage = `Error 404: ws no encontrado!`;
            } else {
              //errorMessage = `Error ${error.status}: ${error.message}`;
              errorMessage = `Se ha producido un error. Por favor reintente ms tarde. Cdigo: ${error.status}`;
            }
          }
        }

        throwErrorToUser(errorMessage);
        return throwError(errorMessage);
      })
    );
  }

}