import { Component, Inject, OnInit } from "@angular/core";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material";
import { Store } from "@ngrx/store";
import * as TurnoSelectors from '../../../../core/store/selectors/turno.selectors';
import * as TurnoActions from '../../../../core/store/actions/turno.actions';
import { Paciente } from "../../../../shared/models/datos.models";
import { DatosFormulario, FiltrosPacientes } from "../../../../shared/models/request.models";
import { FormControl, Validators } from "@angular/forms";
import { Observable } from "rxjs";
  
export interface DialogData {
  filtro: FiltrosPacientes;
}

@Component({
  selector: 'app-busqPaciente-dialog',
  templateUrl: './busqPaciente.component.html',
  styleUrls: ['./busqPaciente.component.css']
})

export class BusqPacienteDialogComponent implements OnInit {

  // filtros
  dniFC = new FormControl('');
  apeNomFC = new FormControl('');

  pacientes$: Observable<Paciente[]>;
  resultados: Paciente[];
  displayedColumnsBusq1: string[] = ['hc', 'dni', 'nombreApellido'];

  loading = false;

  constructor(
    private store: Store<{ formulario: DatosFormulario }>,
    public dialogRef: MatDialogRef<BusqPacienteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {

    store.select(TurnoSelectors.selectPacientesFiltrados).subscribe(
      (pacientes) => {
        this.resultados = pacientes;
        this.loading = false;
      }
    );
  }

  ngOnInit() {
    this.dniFC.setValue(this.data.filtro.dni);
    this.apeNomFC.setValue(this.data.filtro.apenom);
  }

  
  onCancelarClick(): void {
    this.data.filtro = undefined;
    this.store.dispatch(TurnoActions.setPacientes(
      { pacientes: undefined }
    ));
    setTimeout(()=> {
      this.dialogRef.close();
    }, 200)    
  }

  onClickPaciente(seleccion: Paciente) {
    const filtrosPacientes = new FiltrosPacientes();
    filtrosPacientes.codigo = seleccion.codigo;
    this.store.dispatch(TurnoActions.getPacienteByCodigo({filtrosPacientes}));
    //this.store.select(TurnoSelectors.selectPacienteSelected);
    setTimeout(()=> {
      this.store.dispatch(TurnoActions.setPacientes({ pacientes: undefined }));
      this.dialogRef.close();
    }, 200)
  }

  buscarPacientes() {
    this.loading = true;
    const filtrosPacientes = new FiltrosPacientes();
    filtrosPacientes.dni = this.dniFC.value;
    filtrosPacientes.apenom = this.apeNomFC.value;
    this.store.dispatch(TurnoActions.getPacientes({filtrosPacientes}));
    //this.pacientes$ = this.store.select(TurnoSelectors.selectPacientesFiltrados);
  }

  isValid() {
    let result = false;
    if (this.dniFC != undefined && this.dniFC.valid && 
       this.apeNomFC != undefined && this.apeNomFC.valid) {
      if (
        (this.dniFC != undefined && this.dniFC?.value?.toString().trim() != "") ||
        (this.apeNomFC != undefined && this.apeNomFC.value.trim() != "")) {
        result = true;
      }
    }
    return result;
  }
}