import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { catchError, map, mergeMap, switchMap } from 'rxjs/operators';
import * as ErrorActions from '../actions/error.actions';
import * as FormActions from '../actions/form.actions';
import { ServiceService } from './../../services/service.service';


@Injectable()
export class FormEffects {

    constructor(
        private actions$: Actions,
        private formService: ServiceService,
    ) { }
    
    getLocalidades$ = createEffect(() =>
        this.actions$.pipe(
            ofType(FormActions.GET_LOCALIDADES),
            mergeMap(() => this.formService.getLocalidades().pipe(
                map(localidades => ({ type: FormActions.SET_LOCALIDADES, localidades })),
                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );

    getObraSociales$ = createEffect(() =>
        this.actions$.pipe(
            ofType(FormActions.GET_OBRA_SOCIALES),
            mergeMap(() => this.formService.getObrasSociales().pipe(
                map(obrasSociales => ({ type: FormActions.SET_OBRA_SOCIALES, obrasSociales })),
                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );

    getAlertaObraSocial$ = createEffect(() =>
        this.actions$.pipe(
            ofType(FormActions.GET_ALERTAS_OBRA_SOCIAL),
            mergeMap((payload: any) => this.formService.getObraSocial(payload.obraSocial.codigo).pipe(
                map(obraSocial => ({ type: FormActions.SET_ALERTA_OBRA_SOCIAL, obraSocial })),
                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );

    getEspecialidades$ = createEffect(() =>
        this.actions$.pipe(
            ofType(FormActions.GET_ESPECIALIDADES),
            mergeMap(() => this.formService.getEspecialidades().pipe(
                map(especialidades => ({ type: FormActions.SET_ESPECIALIDADES, especialidades })),
                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );

    getEspecialidadesProfesionales$ = createEffect(() =>
        this.actions$.pipe(
            ofType(FormActions.GET_ESPECIALIDADES_PROFESIONALES),
            mergeMap(() => this.formService.getEspecialidadesProfesionales().pipe(
                map(especialidadesProfesionales => ({ type: FormActions.SET_ESPECIALIDADES_PROFESIONALES, especialidadesProfesionales })),
                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );

    getLicencias$ = createEffect(() =>
        this.actions$.pipe(
            ofType(FormActions.GET_LICENCIAS),
            mergeMap((payload: any) => this.formService.getLicencias(
                payload.filtrosLicencias.ep).pipe(
                map(licencias => 
                    ({ type: FormActions.SET_LICENCIAS, licencias })),
                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );

    getEspecialidadesProfesionalesFiltradas$ = createEffect(() =>
        this.actions$.pipe(
            ofType(FormActions.GET_ESPECIALIDADES_PROFESIONALES_FILTRADAS),
            mergeMap((payload: any) => this.formService.getEspecialidadesProfesionalesFiltradas(
                payload.filtrosProfesionales.os, payload.filtrosProfesionales.esp).pipe(
                map(especialidadesProfesionalesFiltradas => 
                    ({ type: FormActions.SET_ESPECIALIDADES_PROFESIONALES_FILTRADAS, especialidadesProfesionalesFiltradas })),
                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );

    getTurnos$ = createEffect(() =>
        this.actions$.pipe(
            ofType(FormActions.GET_TURNOS),
            mergeMap((payload: any) => this.formService.getTurnosFiltrados(payload.filtrosTurnos).pipe(
                map(turnosRta => ({ type: FormActions.SET_TURNOS, turnosRta })),
                catchError((error: Error) =>
                    of({ type: ErrorActions.SHOW_ERROR, error: error.message })
                )
            ))
        )
    );

}
