import { Component, Inject, OnInit } from "@angular/core";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material";
import { Store } from "@ngrx/store";
import { EspecialidadProfesional, Licencia } from "../../../../shared/models/datos.models";
import { DatosFormulario } from "../../../../shared/models/request.models";
import * as FormSelectors from '../../../../core/store/selectors/form.selectors';


export interface DialogData {
  profesional: EspecialidadProfesional
}

@Component({
  selector: 'app-licencias-dialog',
  templateUrl: './licencias.component.html',
  styleUrls: ['./licencias.component.css']
})

export class LicenciasDialogComponent implements OnInit {

  //licencias$: Observable<Licencia[]>;
  licencias: Licencia[];
  displayedColumnsBusq1: string[] = ['desde','hasta', 'motivo' ];

  constructor(
    private store: Store<{ formulario: DatosFormulario }>,
    public dialogRef: MatDialogRef<LicenciasDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {

    //this.licencias$ = store.select(FormSelectors.selectLicencias);
    
    store.select(FormSelectors.selectLicencias).subscribe(
      (licencias) => {
        this.licencias = licencias;
      }
    );
    
  }

  ngOnInit() {

  }

  onCancelarClick(): void {
    this.dialogRef.close();
  }
  
}